.big-img {
    z-index: 5;
    position: absolute;
    top: 0;
    left: 0;
    width: 100vw;
    height: 100vh;
    background: rgba(54, 54, 54, 0.582);
    &__wrapper {
        position: relative;
        width: 100%;
        height: 100%;
    }
    & img {
        position: absolute;
        left: 50%;
        top: 50%;
        transform: translate(-50%, -50%);
        max-width: 80%;
        max-height: 80%;
    }
}