.add-img {
    display: flex;
    justify-content: center;
    padding: 2rem;
    & input {
        margin-right: 1rem;
    }
    &__button {
        border: 1px solid black;
        width: fit-content;
        padding: 0.5rem;
        cursor: pointer;
    }
    &__button:hover {
        transition: all 0.2s;
        background: rgba(255, 132, 31, 0.877);
        color: #fff;
    }
}