.gallery-item {
    display: flex;
    flex-direction: column;
    text-align: center;
    padding: 1rem;
    & img {
        max-width: 250px;
        height: 250px;
        overflow: hidden;
        object-fit: cover;
        cursor: pointer;
        margin: 0 auto;
    }
}

.comment-changer {
    flex-direction: column;
    padding: 1rem 0;
    border: 1px solid black;
    margin-bottom: 1rem;
    & input {
        width: 50%;
        margin-bottom: 1rem;
    }
    &__buttons {
        display: flex;
        justify-content: center;
        & button {
            width: 35%;
        }
    }
}